import React from 'react';
import IconButton from '@material-ui/core/IconButton';
import Visibility from '@material-ui/icons/Visibility';
import Mutation from '../Mutation';
import Query from '../Query';
import { USER_CREDIT_HISTORY } from '../../graphql/query/remote';
import { CREDIT_ENTRY_TABLE } from '../../graphql/mutation/local';
import { CREDIT_ENTRY_TABLE as CREDIT_ENTRY_TABLE_QUERY } from '../../graphql/query/local';
import { defaultState } from '../../graphql/resolvers';
import EnhancedTable from '../EnhancedTable';
import Dialog from './Dialog';

const creditColumnData = [
  { id: 'createdAt', numeric: false, disablePadding: false, colSpan: 1, label: 'Credited On' },
  { id: 'amount', numeric: false, disablePadding: false, colSpan: 1, label: 'Amount (₹)' },
];
const creditExclude = ["__typename", "count"];
class CreditHistory extends React.PureComponent {
  state = {
    open: false,
  };
  componentWillMount() {
    this.setState({
      reset: true
    });
  }
  handleClose = () => {
    this.setState({ open: false });
  };

  handleOpen = () => {
    this.setState({ open: true });
  };

  render() {
    const { open } = this.state;
    const { positiveText = "Ok", positiveHandler = e => null, selected, historyQuery = USER_CREDIT_HISTORY } = this.props;
    return (
      <div>
        <IconButton aria-label="Credit History" onClick={this.handleOpen}>
          <Visibility />
        </IconButton>
        {
          open ? (
            <Dialog
              {...this.props}
              fullScreen={false}
              open={open}
              title={"Credit History"}
              content={
                <Mutation mutation={CREDIT_ENTRY_TABLE}>
                  {(lmutate, args) => {
                    if (this.state.reset) {
                      lmutate({ variables: defaultState.creditEntryTable });
                      this.setState({ reset: false });
                    }
                    return (
                      <Query query={CREDIT_ENTRY_TABLE_QUERY}>
                        {({ loading, error, data: { creditEntryTable } }) => {
                          const { page, rowsPerPage } = creditEntryTable;
                          return (
                            <Query query={historyQuery} variables={{ id: selected, limit: rowsPerPage, skip: page * rowsPerPage }}>
                              {({ loading, error, data }) => {
                                let creditHistory = data[Object.keys(data)[0]];
                                return (
                                  <EnhancedTable
                                    {...this.props}
                                    loading={loading}
                                    data={creditHistory}
                                    title="Users Credit History"
                                    settings={creditEntryTable}
                                    skipToolbar
                                    count={(creditHistory.length && creditHistory[0].count) || 0}
                                    columnData={creditColumnData}
                                    exclude={creditExclude}
                                    mutate={async args => {
                                      await lmutate({ variables: args });
                                      // refetch();
                                    }}>
                                  </EnhancedTable>
                                );
                              }}
                            </Query>
                          );
                        }}
                      </Query>
                    )
                  }}
                </Mutation>
              }
              positiveText={positiveText}
              negativeHandler={e => this.handleClose(e)}
              positiveHandler={e => { this.handleClose(e); positiveHandler(e) }}
            >
            </Dialog>
          ) : null
        }
      </div>
    );
  }
}

export default CreditHistory;


