import React from 'react';
import TableCell from '@material-ui/core/TableCell';
import toCurrency from './toCurrency';

export const RENDER_VALUE = ({ key = "", value, skip = [], balance = true}) => {
  let overrideColor = null;
  if (balance && key.includes("balance")) {
    overrideColor = value < 0 ? NEGATIVE_COLOR : POSITIVE_COLOR;
  }
  else if(!balance){
    overrideColor = value < 0 ? NEGATIVE_COLOR : POSITIVE_COLOR;
  }
  value = (typeof (value) === "number" && !skip.includes(key)) ? toCurrency(value) : value;
  return { value, color: overrideColor };
};
const POSITIVE_COLOR = "rgba(51, 153, 51, 1)";
const NEGATIVE_COLOR = "rgba(255, 0, 0, 1)";
const BACKGROUND_COLOR = "rgba(0, 255, 0, 0.4)";

export default (
  {
    match = "",
    bgcolor = BACKGROUND_COLOR,
    renderValue = RENDER_VALUE
  } = {}) =>
  (key = null, items, colorValue) => {
    if (!(items instanceof Object)) {
      const val = renderValue({value: items, key});
      return (
        <TableCell
          style={{ color: val.color || colorValue, backgroundColor: key === match ? bgcolor : null }}
          key={key || Math.random()}
        >
          {
            val.value
          }
        </TableCell>
      );
    }
    const { id, __typename, paid, ...rest } = items;
    return (
      Object.keys(rest).map(e => {
        const val = renderValue({value: rest[e], key: e});
        return (
          <TableCell
            style={{ color: val.color || colorValue, backgroundColor: paid ? bgcolor : null }}
            key={key ? (key + id + e) : (id + e)}
          >
            {
              val.value
            }
          </TableCell>
        );
      })
    );
  }


