import React from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Checkbox from '@material-ui/core/Checkbox';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import EditIcon from '@material-ui/icons/Edit';
import Visibility from '@material-ui/icons/Visibility';
import FormDialog from './Dialogs/FormDialog';
import { lighten } from '@material-ui/core/styles/colorManipulator';
import formatDate from '../utils/date';
import DeleteDialog from './Dialogs/DeleteDialog';
import CreditHistory from './Dialogs/CreditHistory';
import Loader from './Loader';
import filter from '../utils/filter';
import commanRender from '../utils/commanRender';

// function getSorting(order, orderBy) {
//     return order === 'desc'
//         ? (a, b) => (b[orderBy] < a[orderBy] ? -1 : 1)
//         : (a, b) => (a[orderBy] < b[orderBy] ? -1 : 1);
// }

function* getColor({ primary = "#35A7FF", secondary = "#FF5964" } = {}) {
  let color = primary;
  while (true) {
    color = color === secondary ? primary : secondary;
    yield color;
  }
}

class EnhancedTableHead extends React.PureComponent {
  createSortHandler = property => event => {
    this.props.onRequestSort(event, property);
  };

  render() {
    const {
      selectable,
      onSelectAllClick,
      order,
      orderBy,
      numSelected,
      rowCount,
      columnData
    } = this.props;
    let color = getColor();
    let first = true;
    return (
      <TableHead>
        <TableRow>
          {
            selectable ? (
              <TableCell padding="checkbox">
                <Checkbox
                  indeterminate={numSelected > 0 && numSelected < rowCount}
                  checked={numSelected === rowCount}
                  onChange={onSelectAllClick}
                />

              </TableCell>
            ) : null
          }
          {columnData.map((column, index) => {
            let colorValue = null;
            if (first && column.id === "createdAt") {
              colorValue = null;
              first = false;
            }
            else {
              colorValue = color.next().value;
            }
            return (
              <TableCell
                key={column.id}
                numeric={column.numeric}
                padding={(index !== 0 && column.disablePadding) ? 'none' : 'default'}
                sortDirection={orderBy === column.id ? order : false}
                colSpan={column.colSpan}
              >
                <Tooltip
                  title="Sort"
                  placement={column.numeric ? 'bottom-end' : 'bottom-start'}
                  enterDelay={300}
                >
                  <TableSortLabel
                    active={orderBy === column.id}
                    direction={order}
                    onClick={this.createSortHandler(column.id)}
                    style={{ color: colorValue }}
                  >
                    {column.label}
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
            );
          }, this)}
        </TableRow>
      </TableHead>
    );
  }
}

EnhancedTableHead.propTypes = {
  numSelected: PropTypes.number.isRequired,
  onRequestSort: PropTypes.func.isRequired,
  onSelectAllClick: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired,
  rowCount: PropTypes.number.isRequired,
};

const toolbarStyles = theme => ({
  root: {
    paddingRight: theme.spacing.unit,
  },
  highlight:
    theme.palette.type === 'light'
      ? {
        color: theme.palette.secondary.main,
        backgroundColor: lighten(theme.palette.secondary.light, 0.85),
      }
      : {
        color: theme.palette.text.primary,
        backgroundColor: theme.palette.secondary.dark,
      },
  spacer: {
    flex: '1 1 50%',
  },
  actions: {
    color: theme.palette.text.secondary,
  },
  title: {
    flex: '0 0 auto',
  },
});

let EnhancedTableToolbar = props => {
  const {
    numSelected,
    selected,
    classes,
    title,
    menu,
    historyable,
    options: {
      onView,
      onEdit,
      onDelete,
      onCredit,
      viewable = onView !== onEdit,
      editable = onEdit ? true : false,
      deletable = onDelete ? true : false,
      creditable = onCredit ? true : false,
    },
    credit = (
      <FormDialog
        {...this.props}
        positiveHandler={(m, e) => onCredit(m, { ...e, id: selected[0] })}
      >
      </FormDialog>
    ),
  } = props;
  const deleteJSX = (
    <Grid item xs>
      <Tooltip title="Delete">
        <DeleteDialog
          positiveHandler={e => onDelete({ ids: selected })}
        />
      </Tooltip>
    </Grid>
  );
  return (
    <Toolbar
      className={classNames(classes.root, {
        [classes.highlight]: numSelected > 0,
      })}
    >
      <div className={classes.title}>
        {numSelected > 0 ? (
          <Typography color="inherit" variant="subheading">
            {numSelected} selected
                        </Typography>
        ) : (
            <Typography variant="title" id="tableTitle">
              {title}
            </Typography>
          )}
      </div>
      <div className={classes.spacer} />
      <div className={classes.actions}>
        <Grid container direction="row" justify="space-between">
          {
            numSelected > 0 ? (
              <React.Fragment>
                {
                  numSelected === 1 ? (
                    <React.Fragment>
                      {
                        historyable ?
                          <Grid item xs>
                            <CreditHistory selected={selected[0]} historyQuery={historyable}>
                            </CreditHistory>
                          </Grid>
                          :
                          null
                      }
                      {
                        creditable ?
                          <Grid item xs>
                            <Tooltip title="Credit">
                              {React.cloneElement(credit, { selected: selected[0] })}
                            </Tooltip>
                          </Grid> :
                          null
                      }
                      {
                        viewable ?
                          <Grid item xs>
                            <Tooltip title="View">
                              <IconButton aria-label="View" onClick={e => onView(selected[0])}>
                                <Visibility />
                              </IconButton>
                            </Tooltip>
                          </Grid> :
                          null
                      }
                      {
                        editable ?
                          <Grid item xs>
                            <Tooltip title="Edit">
                              <IconButton aria-label="Edit" onClick={e => onEdit(selected[0])}>
                                <EditIcon />
                              </IconButton>
                            </Tooltip>
                          </Grid> :
                          null
                      }
                      {
                        deletable ? deleteJSX : null
                      }
                    </React.Fragment>
                  ) : (
                      <React.Fragment>
                        {deletable ? deleteJSX : null}
                      </React.Fragment>
                    )
                }
              </React.Fragment>
            ) : null
          }
          <Grid item xs>
            {menu ? menu(selected) : null}
          </Grid>
        </Grid>
      </div>
    </Toolbar >
  );
};

EnhancedTableToolbar.propTypes = {
  classes: PropTypes.object.isRequired,
  numSelected: PropTypes.number.isRequired,
};

EnhancedTableToolbar = withStyles(toolbarStyles)(EnhancedTableToolbar);

const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
  },
  table: {
    minWidth: '100%',
  },
  tableWrapper: {
    overflowX: 'auto',
  },
});

class EnhancedTable extends React.PureComponent {
  handleRequestSort = async (event, property) => {
    const orderBy = property;
    let order = 'asc';

    if (this.props.settings.orderBy === property && this.props.settings.order === 'asc') {
      order = 'desc';
    }
    await this.props.mutate({ ...this.props.settings, order, orderBy });
    // this.setprops({ order, orderBy });
  };

  handleSelectAllClick = async (event, checked) => {
    if (checked) {
      await this.props.mutate({ ...this.props.settings, selected: this.props.data.map(n => n.id) });
      // this.setprops(props => ({ selected: props.data.map(n => n.id) }));
      return;
    }
    await this.props.mutate({ ...this.props.settings, selected: [] });
    // this.setprops({ selected: [] });
  };

  handleClick = async (event, id) => {
    const { selected } = this.props.settings;
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }
    await this.props.mutate({ ...this.props.settings, selected: newSelected });
  };

  handleChangePage = async (event, page) => {
    await this.props.mutate({ ...this.props.settings, page, selected: [] });
  };

  isSelected = id => this.props.settings.selected.indexOf(id) !== -1;

  dialogMutate = dialogOpen => this.props.mutate({ ...this.props.settings, dialogOpen: !dialogOpen })
  render() {
    const {
      classes,
      loading,
      data,
      count,
      settings: {
        order,
        orderBy,
        selected = [],
        rowsPerPage,
        page
      },
      skipToolbar,
      title,
      columnData,
      exclude,
      historyable,
      credit,
      menu,
      options = {},
      customColumnData,
      render = commanRender({})
      // render = (_, value, colorValue) => {
      //   if (!(value instanceof Object)) {
      //     return (
      //       <TableCell style={{ color: colorValue }} key={_ || Math.random()}>{value}</TableCell>
      //     );
      //   }
      //   const { __typename, ...rest } = value;
      //   return (
      //     Object.keys(rest).map(e => (
      //       <TableCell style={{ color: colorValue }} key={_ ? _ + e : e}>{rest[e]}</TableCell>
      //     ))
      //   );
      // },
    } = this.props;
    const {
      onEdit,
      onView = onEdit,
      onDelete,
      onCredit,
      selectable = onEdit || onView || onDelete || onCredit
    } = options;
    const newop = {
      onEdit,
      onView,
      onDelete,
      onCredit,
    };
    if (data.length === 0) {
      return (
        <h1>No Data Yet!</h1>
      );
    }
    const renderData = [].concat(data)
    // .sort(getSorting(order, orderBy));    
    return (
      <Paper className={classes.root}>
        {
          loading ? <Loader></Loader> : null
        }
        {
          skipToolbar ? null :
            <EnhancedTableToolbar
              title={title}
              numSelected={selected.length}
              selected={selected}
              menu={menu}
              historyable={historyable}
              credit={credit}
              options={newop}
            />
        }
        <div className={classes.tableWrapper}>
          <Table className={classes.table} aria-labelledby={title}>
            <EnhancedTableHead
              selectable={selectable}
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={this.handleSelectAllClick}
              onRequestSort={this.handleRequestSort}
              rowCount={data.length || 1}
              columnData={columnData}
            />
            <TableBody>
              {renderData.map(({ createdAt, ...row }, mapIndex) => {                
                const isSelected = this.isSelected(row.id);
                return (
                  <TableRow
                    hover
                    role="checkbox"
                    aria-checked={isSelected}
                    tabIndex={-1}
                    key={row.id || mapIndex}
                    selected={isSelected}
                    onDoubleClick={e => onView && onView(row.id)}
                  >
                    {
                      selectable ? (
                        <TableCell padding="checkbox">
                          <Checkbox checked={isSelected} onClick={event => this.handleClick(event, row.id)} />
                        </TableCell>
                      ) : null
                    }
                    {
                      createdAt ?
                        <TableCell component="th" scope="row" padding={!selectable ? "default" : "none"}>
                          {formatDate(createdAt)}
                        </TableCell>
                        : null
                    }
                    {
                      (() => {
                        const rest = filter(row, exclude);
                        let color = getColor();
                        return (
                          <React.Fragment>
                            {
                              Object.keys(rest).map(e => {
                                let colorValue = color.next().value;
                                return render(e, rest[e], colorValue);
                              })
                            }
                            {
                              customColumnData ? <TableCell style={{ color: color.next().value }} key={row.id || Math.random()}>{customColumnData(rest)}</TableCell> : null
                            }
                          </React.Fragment>
                        );
                      })()
                    }
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </div>
        <TablePagination
          component="div"
          count={count}
          rowsPerPage={rowsPerPage}
          rowsPerPageOptions={[15]}
          page={page}
          backIconButtonProps={{
            'aria-label': 'Previous Page',
          }}
          nextIconButtonProps={{
            'aria-label': 'Next Page',
          }}
          onChangePage={this.handleChangePage}
        />
      </Paper>
    );
  }
}

EnhancedTable.propTypes = {
  classes: PropTypes.object.isRequired,
};

EnhancedTable = withStyles(styles)(EnhancedTable);
export default EnhancedTable;



